

#banner{
  background: linear-gradient(rgba(0,0,0,0.5), #009688), url(images/banner.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.logo{
  width: 140px;
  position: absolute;
  top: 4%;
  left: 10%;
}

.banner-text{
  text-align: center;
  color: #fff;
  padding-top: 180px;
}
.banner-text h1{
  font-size: 130px;
  font-family: 'Kaushan Script', cursive;
}
.banner-text p{
  font-size: 20px;
  font-style: italic;
}

.banner-btn{
  margin: 70px auto 0;
  cursor: pointer;
}
.banner-btn a{
  width: 150px;
  text-decoration: none;
  display: inline-block;
  margin: 0 10px;
  padding: 12px 0;
  color: #fff;
  border: .5px solid #fff;
  position: relative;
  z-index: 1;
  transition: color 0.5s;
}
.banner-btn a span{
  width: 0;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #fff;
  z-index: -1;
  transition: 0.5s;
}
.banner-btn a:hover span{
  width: 100%;
}
.banner-btn a:hover{
  color: #000;
}

.sideNav{
  font-weight: bolder;
  width: 250px;
  height: 100vh;
  position: fixed;
  right: 0;
  top: 0;
  background: #009688;
  z-index: 2;
  color: #fff;
  cursor: pointer;
}
.sideNav.close{
  right: -100%;
  transition: all 0.5s ease-in-out;
}

nav ul li {
  list-style: none;
  margin: 50px 20px;
}
nav ul li a {
  text-decoration: none;
  color: #fff;
}

.menuBtn {
  width: 50px;
  height: 50px;
  background: #009688;
  text-align: center;
  position: fixed;
  right: 30px;
  top: 20px;
  border-radius: 3px;
  z-index: 3;
  cursor: pointer;
}

.menuBtn img {
  width: 20px;
  margin-top: 15px;
  cursor: pointer;
}

@media screen and (max-width:770px) {
  .banner-text h1{
    font-size: 44px;
  }
  .banner-btn a{
    display: block;
    margin: 20px auto;
  }
}

/* features */
#features{
    width: 100%;
    padding: 70px 0;
}

.title-text{
  text-align: center;
  padding-bottom: 70px;
}
.title-text p{
  margin: auto;
  font-size: 20px;
  color: #009688;
  font-weight: bold;
  position: relative;
  z-index: 1;
  display: inline-block;
}
.title-text p::after{
  content: '';
  width: 50px;
  height: 35px;
  background: linear-gradient(#019587, #fff) ;
  position: absolute;
  top: -20px;
  left: 0;
  z-index: -1;
  transform: rotate(10deg);
  border-top-left-radius: 35px;
  border-bottom-right-radius: 35px;
}
.title-text h1{
  font-size: 50px;
}

.feature-box{
  width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
}
.feature{
  flex-basis: 50%;
}

.feature-img{
  flex-basis: 50%;
  margin: auto;
}
.feature-img img{
  width: 70%;
  border-radius: 10px;
}

.feature h1{
  text-align: left;
  margin-bottom: 10px;
  font-weight: 100;
  color: #009688;
}
.feature-desc{
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}
.feature-icon {
  width: 50px;
  height: 50px;
  font-size: 30px;
  line-height: 50px;
  border-radius: 8px;
  color: #009688;
  border: 1px solid #009688;
}

.feature-text{
  padding: 0 20px;
  text-align: initial;
  color: #000;
  font-weight: bold;
}

@media screen and (max-width:770px) {
  .title-text h1{
    font-size: 35px;
  }
  .feature{
    flex-basis: 100%;
  }
  .feature-img{
    flex-basis: 100%;
  }
  .feature-img img{
    width: 100%;
  }
}

/* services */

#service{
    width: 100%;
    padding: 70px 0;
    background: #efefef;
}

.service-box{
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: auto;
}

.single-service{
  flex-basis: 48%;
  text-align: center;
  border-radius: 7px;
  margin-bottom: 20px;
  color: #fff;
  position: relative;
}
.single-service img{
  width: 100%;
  border-radius: 7px;
}
.overlay{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  border-radius: 7px;
  cursor: pointer;
  background: linear-gradient(rgba(0,0,0,0.5), #009688);
  opacity: 0;
  transition: 1s;
}
.single-service:hover .overlay{
  opacity: 1;
}
.service-desc{
  width: 80%;
  position: absolute;
  bottom: 0;
  left: 50%;
  opacity: 0;
  transform: translateX(-50%);
  transition: 1s;
}
hr{
  background: #fff;
  height: 2px;
  border: 0;
  margin: 15px auto;
  width: 60%;
}

.service-desc p {
  font-size: 14px;
}

.single-service:hover .service-desc{
  bottom: 40%;
  opacity: 1;
}

@media screen and (max-width:770px) {
  .single-service {
    flex-basis: 100%;
    margin-bottom: 30px;
  }
  .service-desc p {
    font-size: 12px;
  }
  hr {
    margin: 5px auto;
  }
  .single-service:hover .service-desc{
    bottom: 25% !important;
  }
}

/* Testimonial */

#testimonial{
  width: 100%;
  padding: 70px 0;
}
.testimonial-row{
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}
.testimonial-col {
  flex-basis: 28%;
  padding: 10px;
  margin-bottom: 30px;
  border-radius: 5px;
  box-shadow: 0 10px 20px 3px #00968814;
  cursor: pointer;
  transition: transform 0.5s;
}
.testimonial-col:hover{
  transform: translateY(-10px);
}
.testimonial-col p {
  font-size: 14px;
}
.user {
  display: flex;
  align-items: center;
  margin: 20px 0;
}
.user img {
  width: 40px;
  margin-right: 20px;
  border-radius: 3px;
}
.user-icon {
  margin-left: 10px;
  color: #27c0ff;
  font-size: 20px;
}
.user-info small {
   color: #009688;
   font-weight: bold;
}
.testimonial-col p {
  color: #000;
  font-weight: bold;
}

@media screen and (max-width:770px) {
    .testimonial-col{
      flex-basis: 100%;
    }
}

/* footer */
#footer {
    padding:100px 0 20px;
    background: #efefef;
    position: relative;
}
.footer-row {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.footer-left,.footer-right {
    flex-basis: 45%;
    padding: 10px;
    margin-bottom: 20px;
}
.footer-left p {
  display: flex;
}
.footer-right p {
  display: inline-flex;
}
.footer-right {
    text-align: right;
} 
.footer-row h1 {
    margin: 10px 0 ;
}
.footer-row p {
    line-height: 35px;
    color: #000;
    font-weight: bolder;
}

.footer-icon {
  font-size: 20px;
  color: #009688;
  margin: 10px;
}
.footer-img {
  max-width: 370px;
  opacity: 0.1;
  position: absolute;
  left: 50%;
  top: 35%;
  transform: translate(-50%, -50%);
}

.social-links {
  text-align: center;
}
.social-icon {
  height: 40px;
  width: 40px;
  font-size: 20px;
  line-height: 40px;
  border: 1px solid #009688;
  margin: 40px 5px 0;
  color: #009688;
  cursor: pointer;
  transition: 0.5s;
}
.social-icon:hover {
  background: #009688;
  color: #fff;
  transform: translateY(-7px);
}
.social-links p {
    font-size: 12px;
    margin-top: 20px;
    color: #000;
    font-weight: bold;
}

@media screen and (max-width:770px) {
    .footer-left, .footer-right {
      flex-basis: 100%;
      font-size: 12px;
    }
    .footer-img {
      top: 25%;
    }
}